.header {
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  margin: 45px auto 0;
  max-width: 880px;
  padding-bottom: 41px;
  width: 100%;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}
@media (max-width: 320px) {
  .header {
  margin: 27px auto 0;
  padding-bottom: 31.6px;
  }
}
